body {
    background-color: #0E0F10;
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
  }
  
  header {
    background-color: #59A5FF;
    color: #fff;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
  }
  
  h1 {
    margin: 0;
    font-size: 24px;
    font-weight: normal;
  }
  img{
    height: 60px;
    display: block;
  margin: auto;
  }
  
  a.login-btn:hover {
    background-color: #ccc;
    color: #1e90ff;
    border: none;
  }
  .LeftSlider {
    position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 250px;
  background-color: #091023;
  }
  .navbar h1{
    color: #fff;
    margin: 10px;
  }
  .navbar h2{
    margin-top: 30px;
    color: #fff;
    text-align: center;
  }
  .navbar .Marked{
    color:#000000;
    background-color: white;

  }